#footer {
    background-color: #12152b;
    /* padding: 30px 15%; */
    padding-top: 20px;
    /* padding-left: 200px; */
    /* padding-right: 200px; */
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    position: relative;
    z-index: 2;
    /* margin-top: 70px; */
}

.up-btn-wrapper {
    position: absolute;
    top: -23px;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    background-color: #ef224b;
}

.up-btn {
    position: relative;
    top: -3px;
    font-size: 35px;
    line-height: 50px;
    width: 50px;
    color: white;
    text-align: center; 
}

.footer-icons {
    width: 40vw;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 0;
}

.footer-icons a {
    margin: 0 20px;
}

.footer-i {
    color: white;
    font-size: 25px;
    padding: 0 5px;
}

#footer hr {
    border: none;
    height: 1px;
    width: 60%;
    background-color: rgba(255,255,255,.171);
    margin-bottom: 15px;
}

#footer p {
    font-size: 12px;
    padding-bottom: 10px;
}

@media (max-width: 1300px) {
    .up-btn-wrapper {
        height: 44px;
        width: 44px;
    }
    
    .up-btn {
        font-size: 30px;
        width: 44px;
        top: -5px;
    }
}

@media (max-width: 990px) {
    .up-btn-wrapper {
        height: 40px;
        width: 40px;
        top: -20px;
    }
    
    .up-btn {
        font-size: 25px;
        width: 40px;
        top: -5px;
    }
    .footer-i {
        font-size: 20px;
    }
    #footer p {
        font-size: 11px;
    }
}
